import React, {Component} from 'react';
import {Bar, Line, Pie} from 'react-chartjs-2';

class Charts extends Component {
    constructor(props){
        super(props);
        this.state= {
            data: {
                labels : ["1","2","3","4","5"],
                datasets :[ 
                    {
                        label:"les identités en cours", 
                        backgroundColor:"rgba(255,0,255,0.75)",
                        data : [4,5,1,10,32,2,12]
                    },
                    {
                        label:"les identités ", 
                        backgroundColor:"rgba(0,255,0,0.75)",
                        data : [14,15,21,0,12,24,32]
        
                    }
                ]
            },
            
            chartData: props.chartData
            
        }
        
    }
    
    static defaultProps={
        displayTitle: true,
        displayLegend: true,
        legendPosition:'right',
        location:'City'

    }
    
        

    render(){
        return(
            <section id="subscribers" className="section is-top-padded">
                <span className="has-image">
                    <span className="badge is-feed">
                        <img  alt="" /></span>
                    <h3 className="snapshot image-has-text"> information sur les comptes d'utlisateur </h3>
                   
                </span>
                <div className="tile is-ancestor">
                    <div className="tile is-6 is-parent">
                        <div className="tile is-12 is-child box">
                            <p className="sub-caption"> comptes crées </p>
             <Bar
               data={this.state.chartData}
                options={{
                  title:{
                      display: this.props.displayTitle,
                      text:this.props.location,
                      fontSize:22
                  },
                  legend:{
                      display:this.props.displayLegend,
                      position:this.props.legendPosition 
                  }
                    
                }
            }
            />
            
             </div>
                    </div>
                    <div className="tile is-6 is-vertical is-parent">
                        <div className="tile is-child box">
                            <p className="sub-caption">demande sur les comptes</p>
                <Line
               data={this.state.chartData}
                options={{
                  title:{
                      display: this.props.displayTitle,
                      text:"nombre des comptes ayant un mot de passe faible" ,
                      fontSize:22
                  },
                  legend:{
                      display:this.props.displayLegend,
                      position:this.props.legendPosition 
                  }
                    
                }
                }
            
            />
            </div>
                        <div className="tile is-child box">
                            <p className="sub-caption">nombre des comptes actifs </p>
                <Pie
               data={this.state.chartData}
                options={{
                  title:{
                      display: this.props.displayTitle,
                      text:"nombre de trace ayant une attaque réussite",
                      fontSize:22
                  },
                  legend:{
                      display:this.props.displayLegend,
                      position:this.props.legendPosition 
                  }
                    
                }
                }
                

            />
            
            
             
 </div>
                    </div>
                </div>
            </section>
            
        )
    }
}

export default Charts;